




















import {
    Component,
    Vue,
} from 'vue-property-decorator';

import {
    mapState
} from 'vuex';

import {
    User
} from '@/store/user'

import {
    Geography
} from '@/models/core'
import {
    Core
} from '@/store/core'
import _, {
    forEach
} from 'lodash'
@Component({
    components: {},
    computed: {

    }
})
export default class Graph extends Vue {
    response: boolean = false
    farm: any = User.farm
    series: any = [{
        name: 'Income',
        type: 'column',
        data: [1.4, 2, 2.5, 1.5, 2.5, 2.8, 3.8, 4.6]
    }, {
        name: 'Cashflow',
        type: 'column',
        data: [1.1, 3, 3.1, 4, 4.1, 4.9, 6.5, 8.5]
    }, {
        name: 'Revenue',
        type: 'line',
        data: [20, 29, 37, 36, 44, 45, 50, 58]
    }]

    chartOptions: any = {
        chart: {
            height: 350,
            type: 'line',
            stacked: false
        },
        dataLabels: {
            enabled: false
        },
        stroke: {
            width: [1, 1, 4]
        },
        title: {
            text: 'อัตราการเจริญเติบโตของควาย',
            align: 'left',
            offsetX: 110
        },
        xaxis: {
            categories: [2009, 2010, 2011, 2012, 2013, 2014, 2015, 2016],
        },
        yaxis: [{
                axisTicks: {
                    show: true,
                },
                axisBorder: {
                    show: true,
                    color: '#008FFB'
                },
                labels: {
                    style: {
                        colors: '#008FFB',
                    }
                },
                title: {
                    text: "แรกเกิด",
                    style: {
                        color: '#008FFB',
                    }
                },
                tooltip: {
                    enabled: true
                }
            },
            {
                seriesName: 'Income',
                opposite: true,
                axisTicks: {
                    show: true,
                },
                axisBorder: {
                    show: true,
                    color: '#00E396'
                },
                labels: {
                    style: {
                        colors: '#00E396',
                    }
                },
                title: {
                    text: "240 วัน",
                    style: {
                        color: '#00E396',
                    }
                },
            },
            {
                seriesName: '400 วัน',
                opposite: true,
                axisTicks: {
                    show: true,
                },
                axisBorder: {
                    show: true,
                    color: '#FEB019'
                },
                labels: {
                    style: {
                        colors: '#FEB019',
                    },
                },
                title: {
                    text: "400 วัน",
                    style: {
                        color: '#FEB019',
                    }
                }
            },
            {
                seriesName: '600 วัน',
                opposite: true,
                axisTicks: {
                    show: true,
                },
                axisBorder: {
                    show: true,
                    color: '#FF4560'
                },
                labels: {
                    style: {
                        colors: '#FF4560',
                    },
                },
                title: {
                    text: "600 วัน",
                    style: {
                        color: '#FF4560',
                    }
                }
            },
        ],
        tooltip: {
            fixed: {
                enabled: true,
                position: 'topLeft', // topRight, topLeft, bottomRight, bottomLeft
                offsetY: 30,
                offsetX: 60
            },
        },
        legend: {
            horizontalAlign: 'left',
            offsetX: 40
        }
    }

    async created() {
        let loadGraph = await this.getGraphSeries();
        this.response = loadGraph
    }

    async getGraphSeries() {
        let graph = await Core.getHttp(`/report/evo/${this.farm.id}/`)
        if (graph.success) {
            this.series = graph.series
            this.chartOptions.xaxis.categories = graph.index
            return graph.success
        } else {
            return graph.success
        }
    }

}
